<template lang="pug">
.myMenu
    #menuContainer
        .leftContainer
            router-link.menuItem(v-for='item in menuLeft' :to="item.link") {{item.name}} 
        router-link.middleContainer(to="/") 國立清華大學諮商中心個諮派案系統
        .rightContainer
            div(v-if="isLogin")
                router-link.menuItem(v-for='item in menuRight' :to="item.link") {{item.name}}
            el-icon.userItem(:size="20" color="#4c2c06" @click="drawer = true")
                Expand
            el-drawer(v-model='drawer' :with-header='false' :size="200")
                el-icon(:size="20" style="margin: 20px;cursor: pointer" @click="drawer = false")
                    Close
                template(v-if="isLogin")
                    .mydrawerItem(v-for="item in drawerItem" @click="changeRoute(item.link)") {{item.name}}
                .mydrawerItem(v-if="!isLogin" @click="changeRoute('/login')") 登入
                .mydrawerItem(v-if="isLogin" @click="logout()") 登出
</template>

<script>
import { Expand, Close } from '@element-plus/icons'
import Cookies from 'js-cookie'
export default {
    components: {Expand, Close},
    props: ['isLogin'],
    data () {
        return {
            menuLeft: [
                {link: '/addCase',name: '輸入個案'},
                {link: '/listCases',name: '待派個案'},
                {link: '/result',name: '派案列表'},
                {link: '/listCounselor',name: '心理師班表'}
            ],
            menuRight: [
                {link: '/assigning',name: '初排管理'},  
                {link: '/manageCounselor/add',name: '心理師管理'}
            ],
            drawerItem: [
                {link: '/listAllCases',name: '個案管理'},  
                {link: '/holiday',name: '假期管理'},  
            ],
            drawer: false,
            menuclassList: {
                menuIconContainer: true,
                change: false
            },
        }
    },
    mounted() {
        // window.addEventListener('scroll', this.handleScroll)
    },
    methods: {
        logout() {
            Cookies.remove('login')
            this.$router.push('/login')
        },
        changeRoute(link) {
            this.drawer = false
            this.$router.push(link)
        }
    }
}

</script>

