import { createRouter, createWebHashHistory } from 'vue-router'
import Cookies from 'js-cookie'
const routes = [
  {
    path: '/',
    name: 'Home',
    meta: { requiresAuth: false },
    component: () => import( '../views/Home.vue')
  },
  {
    path: '/login',
    name: 'Login',
    meta: { requiresAuth: false },
    component: () => import( '../views/Login.vue')
  },
  {
    path: '/holiday',
    name: 'holiday',
    meta: { requiresAuth: true },
    component: () => import( '../views/Holiday.vue')
  },
  {
    path: '/addCase',
    name: 'addCase',
    meta: { requiresAuth: false },
    component: () => import( '../views/cases/addCase.vue')
  },
  {
    path: '/modifyCase/:id',
    name: 'modifyCase',
    meta: { requiresAuth: false },
    component: () => import( '../views/cases/modifyCase.vue')
  },
  {
    path: '/modifyCounselor/:id',
    name: 'modifyCounselor',
    meta: { requiresAuth: false },
    component: () => import( '../views/counselors/modifyCounselor.vue')
  },
  {
    path: '/listCases',
    name: 'listCases',
    meta: { requiresAuth: false },
    component: () => import( '../views/cases/listCases.vue')
  },
  {
    path: '/listAllCases',
    name: 'listAllCases',
    meta: { requiresAuth: true },
    component: () => import( '../views/cases/listAllCases.vue')
  },
  {
    path: '/manageCounselor',
    name: 'manageCounselor',
    component: () => import( '../views/counselors/manageCounselor.vue'),
    meta: { requiresAuth: true },
    children: [
      { path: 'add', component: () => import( '../views/counselors/addCounselor.vue') },
      {
        path: 'modify/:id',
        name: 'modifyCounselor',
        component: () => import( '../views/counselors/modifyCounselor.vue')
      }
    ]
  },
  {
    path: '/listCounselor',
    name: 'listCounselor',
    meta: { requiresAuth: false },
    component: () => import( '../views/counselors/listCounselor.vue')
  },
  {
    path: '/assigning',
    name: 'assigning',
    meta: { requiresAuth: true },
    component: () => import( '../views/assign/assigning.vue')
  },
  {
    path: '/result',
    name: 'result',
    meta: { requiresAuth: false },
    component: () => import( '../views/assign/result.vue')
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

router.beforeEach(async(to, from, next) => {
  // 看看 to 和 from 兩個 arguments 會吐回什麼訊息
  console.log('to: ', to)
  console.log('from: ', from)
  
  // 目的路由在meta上是否有設置requireAuth: true
  if (to.meta.requiresAuth) {
    // 獲取Cookies當中的login資訊並取得token
    const info = Cookies.get('login')
    console.log(info)
    if (info) {
      const token = JSON.parse(info).character
      // 如果token不為空，且確實有這個欄位則讓路由變更
      if (token.length > 0 || token === undefined) {
        next()
      } else {
        // 未通過則導回login頁面
        next({ name: 'Login' })
      }
    } else {
      next({ name: 'Login' })
    }
  } else {
    next()
  }
})

export default router
