<template lang="pug">
#app
  myMenu(:isLogin="isLogin")
  router-view(v-if="isRouterAlive" v-slot="{ Component }" v-loading="$store.state.status == 'pending'")
    transition(name="fade")
      component(:is="Component")
</template>

<script>
import myMenu from "@/components/myMenu"
import Cookies from 'js-cookie'
export default {
    name: 'Home',
    components: {myMenu},
    provide() {
      return {
        reload: this.reload
      };
    },
    data () {
        return {
          isLogin: false,
          isRouterAlive: true
        }
    },
    watch: {
      '$route'() {
        if (Cookies.get('login')) {
          this.isLogin = true
        }
        else {
          this.isLogin = false
        }
      }
    },
    created() {
      if (Cookies.get('login')) {
        this.isLogin = true
      }
      this.$store.dispatch('fetchHoliday')
      // this.$store.dispatch('fetchCounselors')
      // this.$store.dispatch('fetchUnassignCases')
    },
    methods: {
      reload() {
        this.isRouterAlive = false;
        this.$nextTick(() => {
          this.isRouterAlive = true;
        });
      }
    }
}
</script>

