import { createStore } from 'vuex'
import axios from 'axios'

export default createStore({
  state: {
    counselors: [],
    unassignCases: [],
    assignCases: [],
    cases: [],
    holiday: [],
    status: "end"
  },
  mutations: {
    SET_COUNSELORS (state, counselors) {
      state.counselors = counselors
      console.log(state.counselors)
    },
    SET_CASE (state, cases) {
      state.cases = cases.data
      console.log(state.cases)
    },
    SET_UNASSIGNCASE (state, cases) {
      state.unassignCases = cases
      console.log(state.unassignCases)
    },
    SET_ASSIGNCASE (state, cases) {
      state.assignCases = cases
      console.log(state.assignCases)
    },
    SET_STATUS (state, status) {
      state.status = status
      console.log(status)
    },
    SET_HOLIDAY (state, status) {
      state.holiday = status
      console.log(state.holiday)
    }
  },
  actions: {
    async fetchHoliday(context) {
      try {
        const data = await axios.get('https://dispatch-system.herokuapp.com/api/holiday')
        context.commit('SET_HOLIDAY', data.data)
      }
      catch (error) {
        console.log(error)
      }
    },
    async fetchCounselors(context) {
      try {
        const data = await axios.get('/api/counselor')
        context.commit('SET_COUNSELORS', data.data.data)
      }
      catch (error) {
          console.log(error)
      }
    },
    async fetchCases(context) {
      try {
        const data = await axios.get('/api/cases')
        context.commit('SET_CASE', data.data)
      }
      catch (error) {
          console.log(error)
      }
    },
    async fetchUnassignCases(context) {
      try {
        const data = await axios.get('/api/cases',{params: {status: "unassign"}})
        let cases = data.data.data
        context.commit('SET_UNASSIGNCASE', cases)
      }
      catch (error) {
          console.log(error)
      }
    },
    async fetchAssignCases(context) {
      try {
        var today = new Date()
        // today.setDate(today.getDate() + 1)
        const data = await axios.get('/api/cases',{params: {status: "assign",end_consultation_date: today.getFullYear()+'-'+(today.getMonth()+1)+'-'+today.getDate()}})
        context.commit('SET_ASSIGNCASE', data.data)
      }
      catch (error) {
        console.log(error)
      }
    }
  },
  modules: {
  }
})
